import {Form} from '../ui/form';
import {World} from '../world';

import {Preset} from './preset';

/**
 * Generates a preset with a single star, with N bodies orbiting it.
 */
export class SimpleStarSystem extends Preset {
  constructor() {
    // Create form
    const form = Form.create('simpleStarSystem');
    form.addSlider('starMass', 0.1, 100000000.0, 1000.0, 'exponential');
    form.addSlider('bodyCount', 1, 1000, 100, 'linear');
    super('simpleStarSystem', form);
  }

  public override generate(world: World): void {
    
  }
}
